* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

header {
  background: #fff;
  color: #0074FF;
  padding: 10px 0;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
}

nav ul {
  list-style: none;
  display: flex;
}

nav ul li {
  margin-left: 20px;
}

nav ul li a {
  color: #2B3674;
  text-decoration: none;
  font-size: 1.1rem;
}

.hero {
  /*background: url('hero-bg.jpg') no-repeat center center/cover;*/
  height: 80vh;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero-content h2 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.btn {
  padding: 10px 20px;
  background: #007bff;
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
  border-radius: 5px;
}

.features {
  padding: 50px 20px;
  background: #f4f4f4;
  text-align: center;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 30px;
}

.feature-item {
  background: #fff;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.about {
  padding: 50px 20px;
  background: #fff;
  text-align: center;
}

.contact {
  padding: 50px 20px;
  background: #f4f4f4;
  text-align: center;
}

form {
  max-width: 600px;
  margin: auto;
}

form input,
form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

form button {
  padding: 10px 20px;
  background: #007bff;
  color: #fff;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}

footer {
  padding: 20px;
  background: #333;
  color: #fff;
  text-align: center;
}